.footer-logo-link {
  text-decoration: none;
  margin-bottom: 38px;
  gap: 11px;
  fill: #283645;

  &:hover {
    fill: @second-color;
  }

  &:active {
    opacity: 0.1;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    align-items: baseline;
  }
}

.footer-logo-link__pink {
  &--hidden {
    display: none;
  }

  @media (min-width: @desktop-width) {
    display: none;

    &--hidden {
      display: block;
    }
  }
}

